import axios from 'axios'
const qs = require('qs')

export default {
  getCity () {
    return axios.get('https://api.bayyu.net/rajaongkir/?q=city')
  },
  getCost (params) {
    return axios.post('https://api.bayyu.net/rajaongkir/?q=cost', qs.stringify(params))
  }
}



